import { useEffect, useState } from 'react'
import { useHistory } from 'react-router-dom'
import { createId } from 'lib/createId'
import { useUpdate } from './useUpdate'
import { message } from 'antd'

type Tag = {
  iconName: string,
  name: string,
  key: '0' | '1',
  id: number,
}

const useTags = () => {
  const [tags, setTags] = useState<Tag[]>([])

  useEffect(() => {
    let localTags = JSON.parse(window.localStorage.getItem('tags') || '[]')
    if (localTags.length === 0) {
      localTags = [
        { iconName: '', name: '', key: '0', id: createId() },
        { iconName: '', name: '', key: '0', id: createId() },
        { iconName: '', name: '', key: '0', id: createId() },
        { iconName: '', name: '', key: '0', id: createId() },
        { iconName: '', name: '', key: '0', id: createId() },
        { iconName: '', name: '', key: '0', id: createId() },
        { iconName: '', name: '', key: '0', id: createId() },
        { iconName: '', name: '', key: '0', id: createId() },
        { iconName: '', name: '', key: '0', id: createId() },
        { iconName: '', name: '', key: '0', id: createId() },
        { iconName: '', name: '', key: '1', id: createId() },
        { iconName: '', name: '', key: '1', id: createId() },
        { iconName: '', name: '', key: '1', id: createId() },
        { iconName: '', name: '', key: '1', id: createId() },
        { iconName: '', name: '', key: '1', id: createId() },
        { iconName: '', name: '', key: '1', id: createId() },
        { iconName: '', name: '', key: '1', id: createId() },
        { iconName: '', name: '', key: '1', id: createId() },
        { iconName: '', name: '', key: '1', id: createId() },
        { iconName: '', name: '', key: '1', id: createId() },
      ]
    }
    setTags(localTags)
  }, [])

  useUpdate(() => {
    window.localStorage.setItem('tags', JSON.stringify(tags))
  }, [tags])

  const findTag = (id: number) => tags.filter(tag => tag.id === id)[0]

  const findIndex = (id: number) => {
    let result = -1
    for (let i = 0; i < tags.length; i++) {
      if (id === tags[i].id) {
        result = i
        break
      }
    }
    return result
  }

  const updateTag = (id: number, { name }: { name: string }) => {
    const index = findIndex(id)
    const tagsClone = JSON.parse(JSON.stringify(tags))
    const iconName = tagsClone[index].iconName
    const key = tagsClone[index].key
    tagsClone.splice(index, 1, { iconName, name, key, id });
    setTags(tagsClone)
  }

  const history = useHistory()
  const deleteTag = (id: number) => {
    history.goBack()
    message.success('')
    setTags(tags.filter(tag => tag.id !== id))
  }

  const addTag = (key: '0' | '1') => {
    const tagName = window.prompt(':')
    if (tagName) {
      const oldTags = tags.map(tag => tag.name)
      if (oldTags.indexOf(tagName) >= 0) {
        return message.warning('')
      }
      setTags([...tags, { iconName: '', name: tagName, key, id: createId() }])
    }
  }

  const getName = (id: number) => {
    const tag = tags.filter(tag => tag.id === id)[0]
    return tag ? [tag.iconName, tag.name] : ''
  }

  return { tags, setTags, findTag, updateTag, deleteTag, findIndex, addTag, getName }
}

export { useTags }